.overlay 
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: rgba(0, 0, 0, 0.5)
  z-index: 1

.modal 
  position: fixed
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  background: #fff
  padding: 20px
  border-radius: 5px
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3)
  z-index: 2

  form 
    display: flex
    flex-direction: column
    align-items: center

  .field
    width: 100%
    display: flex
    justify-content: space-between
    gap: 10px

    label 
      margin-bottom: 5px

    input 
      padding: 5px
      margin-bottom: 10px
      border: 1px solid #ccc
      border-radius: 3px

  button 
    background-color: #007bff
    color: #fff
    border: none
    border-radius: 3px
    padding: 10px 20px
    cursor: pointer

  button:hover 
    background-color: #0056b3

